<template>
  <div id="app">
      <v-header></v-header>
    <el-row class="tac">
      <el-col :span="8">
        <el-menu default-active="2" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" theme="dark" style="border-radius: 0px">
          <el-submenu index="1">
            <template slot="title"><router-link to="/JobPabdect"><p>任务总览</p></router-link></template>
              <el-menu-item index="1-1"><router-link to="/JobPabdect"><span>全部任务</span></router-link></el-menu-item>
              <el-menu-item index="1-2"><router-link to="/WholeJob"><span>全部项目</span></router-link></el-menu-item>
          </el-submenu>
          <el-menu-item index="2"><router-link to="/MyFound"><p>我的创建</p></router-link></el-menu-item>
          <el-menu-item index="3"><router-link to="/FoundJobt"><p>新建任务</p></router-link></el-menu-item>
          <el-submenu index="4">
            <template slot="title"><router-link to="/ManageDeploy"><p>管理配置</p></router-link></template>
              <el-menu-item index="1-1"><span>项目配置</span></el-menu-item>
              <el-menu-item index="1-2"><span>参数配置</span></el-menu-item>
          </el-submenu>
        </el-menu>
      </el-col>
    </el-row>
       <router-view :job="job"></router-view>

  </div>
</template>

<script>
import header from'./components/header/header.vue';
export default{
    data() {
        return{
            job:{}
        }
    },
  created(){
    this.$http.get('api/job').then((response) =>{
      response = response.body;
      if (response.errno===0){
        this.job=response.data;
      }
    });
  },
  // 定义v-header组件
  components: {
    'v-header':header
  },
  methods: {
    handleOpen(key, keyPath){
      console.log(key, keyPath);
    },
    handleClose(key, keyPath){
      console.log(key, keyPath);
    }
  }
}
</script>

<style>
.el-menu-vertical-demo{
  width: 250px;
  height: 1000px;
  border-radius: 0;
}
.el-menu-vertical-demo p{
  font-size: 18px;
  color: #fff;
  margin-left: 50px;
}
.el-menu-vertical-demo span{
  font-size: 16px;
  color: #fff;
  margin-left: 70px;
}
  template{
    border-bottom: 1px solid;
  }
</style>



