// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import VueRouter from 'vue-router'
import VuereRource from 'vue-resource'
import JobPabdect from './components/JobPabdect/JobPabdect.vue'
import WholeJob from './components/WholeJob/WholeJob.vue'

Vue.use(VueRouter);
Vue.use(VuereRource);
Vue.use(ElementUI);


Vue.config.productionTip = false;

// 定义路由
const routes = [
        { path: '/JobPabdect', component: JobPabdect },
        { path: '/WholeJob', component: WholeJob }
    ];
    // 创建实例
const router = new VueRouter({
        routes
    });
    //创建和挂载实例
const app = new Vue({
    router,
    render: h => h(App)
}).$mount('#app');



