<template>
<div class="WholeJob">
  <div class="WholeJob-1">
    <div class="block">
      <span class="wrapper">
        <el-button :plain="true" type="info">全部任务</el-button>
        <el-button :plain="true" type="success">成功任务</el-button>
        <el-button :plain="true" type="danger">失败任务</el-button>
        <el-button :plain="true" type="warning">正在执行</el-button>
      </span>
    </div>
  </div>
  <!--搜索条件-->
  <div class="WholeJob-2">
    <el-select v-model="value4" clearable placeholder="请选择搜索条件">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <div class="block-time">
      <el-date-picker
        v-model="value6"
        type="daterange"
        placeholder="选择日期范围">
      </el-date-picker>
    </div>
  </div>
  <!--搜索框-->
  <div class="WholeJob-3">
    <el-input
      placeholder="请输入项目名称"
      icon="search"
      v-model="input2"
      :on-icon-click="handleIconClick">
    </el-input>
  </div>
  <div class="WholeJob-4">
    <el-button><span>搜索</span></el-button>
  </div>
  <div class="WholeJob-5">
    <table>
      <tr>
        <th>序号</th>
        <th>任务ID</th>
        <th>任务名称</th>
        <th>任务类型</th>
        <th>执行时间</th>
        <th>平均耗时</th>
      </tr>
      <tr>
        <td>{{tableData.num}}</td>
        <td>{{tableData.id}}</td>
        <td>{{tableData.name}}</td>
        <td>{{tableData.type}}</td>
        <td>{{tableData.time}}</td>
        <td>{{tableData.times}}</td>
      </tr>
    </table>
  </div>
</div>
</template>

<script>
export default{
  data() {
    return {
      value6: '',
      tableData:[],
      options: [{
        value: '选项1',
        label: '筛选条件1'
      }, {
        value: '选项2',
        label: '筛选条件2'
      }, {
        value: '选项3',
        label: '筛选条件3'
      }, {
        value: '选项4',
        label: '筛选条件4'
      }, {
        value: '选项5',
        label: '筛选条件5'
      }],
      value4: '',
      input2: ''
    }
  },
  methods: {
    handleIconClick(ev) {

    }
  },
  props: {
    job: {
      type: Object
    }
  },
  created(){
      this.$http.get('/api/tableData').then((response) =>{
          response = response.body;
          if (response.errno ===0){
              this.tableData = response.data;
          }
      })
  }
}
</script>

<style>
.WholeJob{
  display: block;
  position: absolute;
  height: 880px;
  width: 1670px;
  left: 250px;
  top: 60px;
}
  .block{
    margin-top: 50px;
    margin-left: 50px;
  }
  span{
    font-size: 18px;
  }
  .block-time{
    margin-left: 250px;
    margin-top: -36px;
  }
  .WholeJob-2{
    width: 300px;
    margin-top: 50px;
    margin-left: 50px;
  }
.WholeJob-3,.WholeJob-4{
  width: 300px;
  margin-top: 50px;
  margin-left: 50px;
  float: left;
}
.WholeJob-4 span{
  font-size: 14px;
}
  .WholeJob-5{
    width: 1300px;
    margin-top: 120px;
    margin-left: 50px;
  }
</style>



